// Notyf 客製化樣式

.notyf {
	&__toast {
		$size: rem-calc(156);

		margin-top: 0;
		width: $size;
		height: $size;
		border-radius: rem-calc(12);
		background: rgba($black, 0.6) !important;

		// 移除淡出時間差
		&--disappear {
			animation-delay: 0s;

			.notyf__message {
				animation-delay: 0s;
			}
		}
	}

	&__wrapper {
		padding: 0;
		justify-content: center;
		flex-direction: column;
		height: 100%;
		width: 100%;
	}

	&__icon {
		margin-bottom: rem-calc(20);
		margin-right: 0;
		width: auto;

		@at-root %icon {
			$size: rem-calc(48);

			width: $size;
			height: $size;
			background: transparent;
			border: 2.5px solid currentColor;
			color: $white !important;

			&::before,
			&::after {
				width: 2.5px;
				border-radius: 9px;
			}
		}

		&--success {
			@extend %icon;

			&::before {
				top: 13px;
				left: 25px;
				height: 20px;
			}

			&::after {
				top: 21px;
				left: 15px;
				height: 11px;
			}
		}

		&--error {
			@extend %icon;

			&::before,
			&::after {
				top: 13px;
				left: 20px;
				height: 20px;
			}
		}
	}
}
