// Checkbox & Radio Style
$checkbox-main-color: $primary-color;
$checkbox-label-font-size: rem-calc(15);
$checkbox-label-line-height: 1.2;
$checkbox-label-color: $bfBlueDark;
$checkbox-border-color: #aaa9a9;
$checkbox-border-radius: rem-calc(5);
$checkbox-hover-labelColor: $bfBlueDark;
$checkbox-hover-border-color: $checkbox-main-color;
$checkbox-hover-bg-color: lighten($checkbox-main-color, 50%);
$checkbox-checked-checkerColor: #fff;
$checkbox-checked-color: $primary-dark;
$checkbox-checked-bg-color: $checkbox-main-color;
$checkbox-checked-border-color: $checkbox-main-color;
$checkbox-checked-hover-border-color: $checkbox-main-color;
$checkbox-checked-hover-bg-color: $checkbox-main-color;
$checkbox-checked-hover-color: $bfBlueDark;
$checkbox-disable_border-color: #dedede;
$checkbox-disable_bg-color: #ebedf0;

// Checker (Checkbox打勾)
$checkbox-checker-border-width: 2px;
$checkbox-checker-width: 12px;
$checkbox-checker-height: 8px;

// Other
$checkbox-size: 20px; // Checkbox 外框
$radio-size-inside: 10px; // Radio 內距

@mixin checkbox-style($type: 'checkbox') {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	margin-bottom: 0;

	+ label {
		position: relative;
		margin-left: 0;
		padding-left: $checkbox-size + 8;
		font-size: $checkbox-label-font-size;
		line-height: $checkbox-label-line-height;
		color: $checkbox-label-color;
		word-break: break-all;
		word-break: break-word;

		// 外框
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: $checkbox-label-line-height / 2 * 1em;
			width: $checkbox-size;
			height: $checkbox-size;
			display: inline-block;
			background: $white;
			border: 1px solid $checkbox-border-color;
			transform: translateY(-50%);
			transition: all 0.3s;

			@if $type == 'checkbox' {
				border-radius: $checkbox-border-radius;
			} @else {
				// Radio Style
				border-radius: 99px;
			}
		}

		// Checker 樣式
		&:after {
			content: '';
			position: absolute;
			border: 0 solid $checkbox-checked-checkerColor;

			@if $type == 'checkbox' {
				$left: round(($checkbox-size - ($checkbox-checker-width + $checkbox-checker-height) / 1.414) / 2);

				top: $checkbox-label-line-height / 2 * 1em;
				left: $left;
				width: 0;
				height: 0;
				border: 0 solid $checkbox-checked-checkerColor;
				border-top: 0 none;
				border-right: 0 none;
				transform: rotate(-45deg);
				transform-origin: left top;
			} @else {
				// [START] Radio Style
				$left: ($checkbox-size - $radio-size-inside) / 2;

				top: $checkbox-label-line-height / 2 * 1em;
				left: $left;
				margin-top: $radio-size-inside / 2 * (-1);
				width: $radio-size-inside;
				height: $radio-size-inside;
				border-radius: 9px;
				background: $checkbox-main-color;
				opacity: 0;
				transform: scale(0);
				transition: all 0.3s;
				// [END] Radio Style
			}
		}
	}

	// Checked =========================================
	&:checked {
		+ label {
			// cursor: default;

			@if $type == 'checkbox' {
				color: $checkbox-checked-color;
			}

			&::before {
				border-color: $checkbox-checked-border-color;

				@if $type == 'checkbox' {
					background: $checkbox-checked-bg-color;
				}
			}

			&:after {
				@if $type == 'checkbox' {
					border-width: $checkbox-checker-border-width;
					animation: checkbox-checked 0.5s forwards;
				} @else {
					opacity: 1;
					transform: scale(1);
					transition-timing-function: cubic-bezier(0.52, -0.5, 0.5, 2);
				}
			}

			// Checked + Hover
			&:hover {
				color: $checkbox-checked-hover-color;

				&:before {
					border-color: $checkbox-checked-hover-border-color;

					@if $type == 'checkbox' {
						background: $checkbox-checked-hover-bg-color;
					}
				}
			}
		}

		&:focus {
			+ label {
				color: $checkbox-checked-hover-color;

				&:before {
					border-color: $checkbox-checked-hover-border-color;

					@if $type == 'checkbox' {
						background: $checkbox-checked-hover-bg-color;
					}
				}
			}
		}
	}

	// Normal =========================================
	// Hover
	&:not(:checked) {
		+ label {
			&:hover {
				&:before {
					border-color: $checkbox-hover-border-color;

					@if $type == 'checkbox' {
						background: $checkbox-hover-bg-color;
					}
				}

				&:after {
					@if $type == 'checkbox' {
					} @else {
						opacity: 0.5;
						transform: scale(0.5);
					}
				}
			}
		}
	}

	// Focus
	&:focus {
		+ label {
			&::before {
				background: $checkbox-hover-bg-color;
				border-color: $checkbox-hover-border-color;
				border-width: 2px;
			}
		}
	}

	// Disabled =========================================
	&:disabled {
		+ label {
			cursor: not-allowed;

			&:before {
				background: $checkbox-disable_bg-color;
				border-color: $checkbox-disable_border-color;
			}

			&:hover {
				&:before {
					background: $checkbox-disable_bg-color;
					border-color: $checkbox-disable_border-color;
				}
			}
		}
	}
}

/* Checkbox */
%checkbox {
	@include checkbox-style('checkbox');
}

.checkbox {
	@extend %checkbox;
}

// input[type="checkbox"] {
// 	@extend %checkbox;
// }

@keyframes checkbox-checked {
	50% {
		width: 0;
		height: $checkbox-checker-height;
	}

	100% {
		width: $checkbox-checker-width;
		height: $checkbox-checker-height;
	}
}

// ------

/* Radio */
%radiobox {
	@include checkbox-style('radio');
}

.radiobox {
	@extend %radiobox;
}

// input[type="radio"] {
// 	@extend %radiobox;
// }

// --------------------------------------------------------

// checkbox 外層
.checkbox-box {
	position: relative;

	.link {
		color: inherit;
		text-decoration: underline;
	}
}

// 多組 checkbox 的外層
.checkbox-group {
	.checkbox-box {
		&:not(:last-child) {
			margin-bottom: rem-calc(10);
		}
	}
}

// Radio 外層
.radio-group {
}

.radio-box {
	lable {
		margin-right: 0;
	}
}
