/* Page Nav */
.page-nav {
	$root: &;

	&__inside {
		border-bottom: 1px solid rgba($black, 0.04);
	}

	&__list {
		display: flex;
		margin-bottom: 0;

		&.bg-transparent {
			background: transparent;
		}
	}

	&__item {
		flex: 1;
		text-align: center;

		&.is-active {
			#{$root} {
				&__link {
					&__text {
						color: $title-dark;

						&::before {
							transform: scaleX(1);
							opacity: 1;
						}
					}
				}
			}
		}
	}

	&__link {
		$activeLineWidth: rem-calc(68);

		position: relative;
		display: block;
		padding: rem-calc(15 10);
		line-height: 1.2;
		color: $bfDarkGray;
		font-weight: $font-bold-m;
		transition: 0.24s color;

		&__text {
			&::before {
				content: '';
				position: absolute;
				bottom: rem-calc(-1);
				left: calc(50% - #{$activeLineWidth / 2});
				display: block;
				width: $activeLineWidth;
				height: rem-calc(3);
				background: $primary-color;
				transform: scaleX(0);
				transition: transform 0.3s, opacity 0.4s;
				opacity: 0;
			}
		}

		&.is-active {
			color: $title-dark;

			#{$root} {
				&__link {
					&__text {
						&::before {
							transform: scaleX(1);
							opacity: 1;
						}
					}
				}
			}
		}

		// @include breakpoint-hover {
		// 	&:hover {
		// 		color: $primary-color;
		// 	}

		// 	&:focus {
		// 		color: $bfDarkGray;
		// 	}
		// }

		@include breakpoint-hover(false) {
			&:hover,
			&:focus {
				color: $title-dark;
			}
		}
	}

	// 固定
	&.is-fixed {
		height: rem-calc(46);

		#{$root} {
			&__inside {
				position: fixed;
				z-index: 500;
				top: 56px; // 用 rem 手機版firefox 會出現縫隙，所以改用 px
				left: 0;
				width: 100%;
				background: $white;
				box-shadow: $shadow-panel;
			}
		}
	}
}

/* Page Nav Tab */
.page-nav__tabs {
	$item: '.page-nav';

	&-title {
		display: flex;
		margin-bottom: 0;
		width: 100%;
		border: 0;

		#{$item}__list {
			border: 0;
		}

		#{$item}__item {
			float: none;

			&.is-active {
				#{$item}__link__text {
					&::before {
						transform: scaleX(1);
					}
				}
			}
		}

		#{$item}__inside {
			width: 100%;
		}
	}

	&-cont {
		// 裡面有 page-category 不需要 padding
		&:not(.p-0) {
			padding: rem-calc(20) 0;
		}
	}
}
