// 說明 (完整說明見) src/sass/style.sass
//	* 此檔案包含：Layout樣式 Loader、Header、Footer

// -------------------------------------------------------

/* Loader */
.loader {
	$root: &;

	position: fixed;
	z-index: 10000;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	background: rgba($white, 0.5);

	&__img-box {
		position: absolute;
		z-index: 1;
		left: 50%;
		top: 50%;
		margin-top: rem-calc(-50);
		margin-left: rem-calc(-50);
		width: 100px;
		height: 100px;
		background: $white;
		border-radius: 99px;

		@at-root %loader-circle {
			content: '';
			position: absolute;
			z-index: -1;
			left: 0%;
			top: 0%;
			width: 100%;
			height: 100%;
			background: $white;
			opacity: 0.3;
			border-radius: 99px;
		}

		&::before {
			@extend %loader-circle;

			transform: scale(1.2);
		}

		&::after {
			@extend %loader-circle;

			transform: scale(1.4);
		}
	}

	&__svg-wrapper {
		position: absolute;
		left: 50%;
		top: 50%;
		margin-top: rem-calc(-23 + 3); // 3: 往下3px
		margin-left: rem-calc(-20);
		width: 46px;
		height: 40px;
	}

	.bean {
		transform-origin: 17.5px 39.5px;
		animation: bean-shake-body 2s infinite alternate;
	}

	%bean-eye {
		display: none;
		animation: bean-eyes-blink 2s infinite;
	}

	.eye-r {
		@extend %bean-eye;

		transform-origin: 10px 14.5px;
	}

	.eye-l {
		@extend %bean-eye;

		transform-origin: 19px 16.5px;
	}

	&.is-loaded {
		opacity: 0;
		transition: opacity 0.5s 1s;
		pointer-events: none;

		#{$root} {
			&__img-box {
				&,
				&::before,
				&::after {
					transform: scale(0);
					transition: transform 0.5s 0.8s;
				}

				&::before {
					transition-delay: 0.7s;
				}

				&::after {
					transition-delay: 0.6s;
				}
			}
		}

		.bean {
			animation-play-state: paused;
		}

		.eye-r,
		.eye-l {
			display: block;
		}

		.eye-r-sleep,
		.eye-l-sleep {
			display: none;
		}
	}
}

@keyframes bean-shake-body {
	0% {
		transform: rotate(0);
	}

	100% {
		transform: rotate(-20deg);
	}
}

@keyframes bean-eyes-blink {
	0% {
		transform: rotate(16deg) scale(1, 0.05);
	}

	10%,
	100% {
		transform: rotate(16deg) scale(1, 1);
	}
}

// -------------------------------------------------------

/* Header - 主要 */

// 品牌Logo
.brand-logo {
	position: relative;
	display: block;
	overflow: hidden;
	width: rem-calc(95);
	margin-bottom: 0;

	@include breakpoint(medium) {
		width: rem-calc(117);
	}

	@include breakpoint(large) {
		width: rem-calc(154);
	}

	&__link {
		display: block;

		// 撐高度
		&::before {
			@include before-img-size(154, 42, true);
		}
	}

	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		transition: opacity 0.3s;

		@include bem-tag(svg) {
			opacity: 1;
		}
	}

	@include breakpoint-hover {
		$logo-width: 154 + 20; // xlarge Logo Width

		transition: opacity 0.5s;

		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: -20px;
			width: 20px;
			height: 100%;
			background: linear-gradient(to right, rgba($white, 0) 0%, $white 50%, rgba($white, 0) 100%);
			transform: skewX(-30deg);
			transform-origin: right top;
			filter: blur(2px);
		}

		// 跳動 Logo
		path {
			@for $i from 1 through 6 {
				&:nth-child(#{$i}) {
					animation-delay: 0.1s * ($i - 1);
				}
			}
		}

		&:hover,
		&:focus {
			&::after {
				transform: skewX(-30deg) translateX(#{percentage(($logo-width + 20 * 2) / 20)});
				transition: transform 0.5s;
			}

			// 跳動 Logo
			path {
				animation-name: jump;
				animation-duration: 0.3s;

				@keyframes jump {
					0%,
					100% {
						transform: translateY(0);
					}

					50% {
						transform: translateY(-3px);
					}
				}
			}
		}
	}
}

.hd {
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	width: 100%;
	color: $white;

	// 下滑後的固定樣式
	&.is-collapse {
		.brand-logo {
			&__img {
				fill: $primary-color; // For Svg Inline
			}
		}

		.hd-main {
			box-shadow: $shadow-panel;

			@include breakpoint(large) {
				padding-top: rem-calc(16);
				padding-bottom: rem-calc(16);
			}

			@include breakpoint-between(large, xlarge) {
				padding-top: rem-calc(16 - 9);
				padding-bottom: rem-calc(16 - 9);
			}
		}

		&.hide-shadow .hd-main {
			box-shadow: none;
		}
	}

	// 透明
	&.transparent {
		&:not(.is-collapse) {
			.brand-logo {
				path {
					fill: $white; // For Svg Inline
				}
			}

			.hd-main {
				background: transparent;

				&__r {
					&,
					a {
						color: $white;
					}
				}
			}

			.hd-search {
				@include breakpoint(large) {
					&__btn-reset {
						@include breakpoint-hover {
							&:hover,
							&:focus {
								opacity: 0.8;
							}
						}
					}

					&__btn-reset {
						color: $white;
					}

					&__btn-submit {
						color: $white;

						@include breakpoint-hover {
							&:not(:disabled) {
								&:hover,
								&:focus {
									opacity: 0.8;
								}
							}
						}
					}

					&__input {
						color: $white;
						background-color: rgba(#fdfdfd, 0.2);

						&::placeholder {
							color: $white;
						}

						&:focus {
							color: $primary-dark;

							&::placeholder {
								color: $input-placeholder-color;
							}

							~ .hd-search {
								&__btn-reset {
									color: #999;
								}

								&__btn-submit {
									color: #999;
									&:not(:disabled) {
										color: $primary-color;
									}
								}
							}
						}
					}
				}
			}
		}
	}

	// 必要時候隱藏 Shadow（包含APP）
	// 不包含APP的後綴有 -app (src\sass\app\layout\_app-layout.scss)
	&.hide-shadow {
		@at-root %shadow-hidden-style {
			box-shadow: none !important;
		}

		@at-root %shadow-hidden {
			@extend %shadow-hidden-style;

			.hd-main {
				@extend %shadow-hidden-style;
			}

			.hd-app {
				@extend %shadow-hidden-style;
			}
		}

		@extend %shadow-hidden;

		&.is-collapse {
			@extend %shadow-hidden;
		}

		&.transparent {
			@extend %shadow-hidden;
		}
	}
}

.hd-main {
	$root: &;

	display: flex;
	justify-content: space-between;
	padding: rem-calc(6 20);
	padding-right: rem-calc(10);
	width: 100%;
	background: $white;

	@include breakpoint-hover {
		transition: 0.3s;
	}

	&,
	a {
		color: $primary-dark;
	}

	@at-root %hd-main-cell {
		display: flex;
		align-items: center;
	}

	&__l {
		@extend %hd-main-cell;

		flex: 1;
	}

	&__c {
		@extend %hd-main-cell;
	}

	&__r {
		@extend %hd-main-cell;

		flex: 1;
		justify-content: flex-end;

		&__btn {
			min-width: 44px;
			min-height: 44px;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: rem-calc(4);
			font-weight: $font-bold-m;
			color: currentColor;

			.be-icon {
				font-size: rem-calc(22);
			}
		}
	}

	@include breakpoint(medium down) {
		&__r {
			&,
			a {
				color: #999;
			}

			&__btn {
				.text {
					@include element-invisible;
				}
			}
		}

		@at-root %hd-mobile-hide {
			display: none;
		}

		// Header - APP 下載
		.hd-download {
			@extend %hd-mobile-hide;
		}

		// Header - 會員按鈕
		.hd-member {
			@extend %hd-mobile-hide;
		}
	}

	@include breakpoint(large) {
		padding: rem-calc(40);

		&__c {
			width: rem-calc(550);
			max-width: 50%;
		}

		&__r {
			margin-right: rem-calc(-6 - 14); // 配合視覺稿，往右推

			&,
			a {
				color: $primary-dark;
			}

			&__item {
				position: relative;

				// 分隔線
				&:not(:last-child) {
					&::after {
						content: '';
						position: absolute;
						top: 50%;
						right: 0;
						width: 1px;
						height: calc(100% - 33px);
						margin-top: rem-calc(2);
						background: currentColor;
						opacity: 0.5;
						transform: translateY(-50%);
					}
				}
			}

			&__btn {
				display: flex;
				align-items: center;
				padding: rem-calc(10 12);

				.be-icon {
					margin-right: rem-calc(1);
					font-size: rem-calc(23);
				}

				@include breakpoint-hover {
					transition: transform 0.3s;

					&:hover,
					&:focus {
						font-weight: $font-bold-b;
						transform: translateY(-3px);
					}
				}
			}
		}

		// Header - 搜尋 (Button Only, Mobile Only)
		.hd-btn-search {
			display: none;
		}
	}

	// 螢幕介面寬度不夠，更改的特殊排版
	@include breakpoint-between(large, xlarge) {
		padding-top: rem-calc(30);
		padding-top: rem-calc(30);

		&__r {
			&__btn {
				font-size: rem-calc(12);
				flex-direction: column;

				.be-icon {
					margin-right: 0;
				}
			}
		}

		.hd-member {
			#{$root} {
				&__r {
					&__btn {
						flex-direction: column-reverse;
					}
				}
			}
		}
	}
}

// Header - APP 下載
.hd-download {
	.hd-main__r__btn {
		padding-right: rem-calc(12 + 1);
	}
}

// Header - 購物車
.hd-cart {
	.hd-main__r__btn {
		padding-left: rem-calc(12 - 5);
	}

	.be-icon {
	}
}

// Header - 會員按鈕
.hd-member {
	.hd-main__r__btn {
		padding-left: rem-calc(12 + 1);
	}

	&__name {
		max-width: 5.3em;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}

	.be-icon {
		margin-right: 0;

		// &::before {
		// 	transform: scale(#{(25 / 23)});
		// }
	}

	img {
		$size: rem-calc(24);

		width: $size;
		height: $size;
		border-radius: 100%;
	}
}

/* Header - 搜尋 */
.hd-search {
	$root: &;

	// 關閉按鈕
	&__btn-close {
		margin-right: rem-calc(-18);
		padding: rem-calc(9 18);
		padding-bottom: rem-calc(10); // 湊32
		font-size: rem-calc(13);
	}

	&__input-box {
		position: relative;
	}

	// 清除按鈕
	&__btn-reset {
		position: absolute;
		top: 50%;
		right: 9px;
		font-size: rem-calc(18 * 0.75);
		color: #ccc;
		transform: translateY(-50%);
		display: none;
	}

	// 送出按鈕，搜尋 Icon
	&__btn-submit {
		position: absolute;
		top: 50%;
		left: 9px;
		// font-size: em-calc(18 / 15);
		font-size: rem-calc(18);
		color: $bfMediumGray;
		transform: translateY(-50%);

		&:disabled {
			color: $bfMediumGray;
			cursor: not-allowed;
		}
	}

	// 關鍵字列表
	&__keyword-list {
		margin: 0;
		list-style: none;
	}

	// 搜尋 Input
	&__input {
		$el: &;

		margin-bottom: 0;
		padding-left: rem-calc(36);
		padding-right: rem-calc(36);
		font-weight: $font-bold-m;
		line-height: rem-calc(32);
		color: $primary-dark;
		background-color: #f4f4f4;
		border-color: transparent;
		border-radius: 99px;
		box-shadow: none;

		@include breakpoint(large) {
			line-height: rem-calc(44);
		}

		@include ie {
			line-height: 1; // IE 要這樣設定
		}

		&:focus {
			border-color: #ccc;
			box-shadow: none;
		}
	}

	// 結果標題
	&__result {
		$el: &;

		color: $primary-dark;
		font-size: rem-calc(15);

		// 關鍵字標題
		&__title {
			font-weight: 600;
		}

		&__list {
			margin: 0;
			list-style: none;
		}

		&__link {
			display: block;
			padding: rem-calc(3 7);
			line-height: 1.38;
			border-radius: rem-calc(4);

			@include bem-tag {
				color: #666;
			}
		}

		&__block {
			&[data-layout='recommend'] {
				#{$el} {
					&__list {
						display: flex;
						flex-wrap: wrap;
					}

					&__item {
						display: inline-block;
					}
				}
			}
		}
	}

	&.is-searching {
		#{$root} {
			&__btn-reset {
				display: block;
			}
		}
	}

	// 手機板樣式
	@include breakpoint(medium down) {
		position: fixed;
		top: 0;
		left: 0;
		display: none; // Hide Panel
		width: 100%;
		height: 100%;
		animation: popup-from-top 0.3s;

		&.is-open {
			display: block;
		}

		// 為了點擊收合的物件
		&__bg {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100vh;
		}

		// 內容區（.grid-container）
		$padding-x: rem-calc(20);
		$padding-y: rem-calc(12);

		&__cont {
			position: relative;
			z-index: 1;
			height: 100%;
			background: $white;
		}

		&__top-box {
			display: flex;
			padding: $padding-y $padding-x;

			&__l {
				flex: 1;
			}

			&__r {
				display: flex;
				align-items: center;
				justify-content: center;
			}
		}

		&__input {
			height: rem-calc(32);
			font-size: rem-calc(13);
		}

		// 不讓使用者觸擊
		&__btn-submit {
			pointer-events: none;
		}

		&__result {
			margin-top: rem-calc(17);
			padding: 0 $padding-x;

			// 關鍵字標題
			&__title {
				margin-bottom: rem-calc(18);
			}

			// 關鍵字
			&__item {
				margin-bottom: rem-calc(12);

				&:not(:last-child) {
					margin-right: rem-calc(12);
				}
			}

			// 關鍵字 - 連結
			&__link {
				border: 1px solid #999;
			}
		}
	}

	// Large 版本
	@include breakpoint(large) {
		$search-border-radius: 22px;

		position: relative;
		width: 100%;
		border: solid 1px transparent;

		&__btn-close {
			display: none;
		}

		&__btn-reset {
			position: absolute;
			top: 50%;
			right: rem-calc(58);
			margin-right: rem-calc(-4);
			padding: rem-calc(4);
			min-width: rem-calc(44);
			min-height: rem-calc(44);
			font-size: rem-calc(18 * 18 / 15 * 0.75);
			color: #cbcbcb;
			transform: translateY(-50%);
		}

		&__btn-submit {
			left: auto;
			right: rem-calc(20 - 4);
			padding: rem-calc(4);
			min-width: rem-calc(44);
			min-height: rem-calc(44);
			font-size: rem-calc(18 * 18 / 15);
			color: $primary-dark;
		}

		&__input {
			padding-right: rem-calc(100);
			padding-left: rem-calc(20);
			height: rem-calc(44);
			font-size: rem-calc(15);
			line-height: 1;
			color: $primary-dark;
			background: #f4f4f4;

			&::placeholder {
				color: #999;
			}

			&:focus {
				&::placeholder {
					color: $input-placeholder-color;
				}

				~ #{$root} {
					&__btn-reset {
						color: #ccc;

						@include breakpoint-hover {
							&:hover,
							&:focus {
								color: $primary-color;
							}
						}
					}

					&__btn-submit {
						&:not(:disabled) {
							color: $primary-color;

							@include breakpoint-hover {
								&:hover,
								&:focus {
									color: darken($primary-color, 10%);
								}
							}
						}
					}
				}
			}
		}

		&__result {
			$el: &;

			position: absolute;
			left: -1px;
			display: none;
			width: calc(100% + 2px); // Border
			padding: rem-calc(9 20);
			padding-bottom: rem-calc(8);
			font-size: rem-calc(15);
			background: $white;
			border: solid 1px #ddd;
			border-top: 0 none;
			border-bottom-right-radius: $search-border-radius;
			border-bottom-left-radius: $search-border-radius;

			// 關鍵字 - 連結
			&__link {
				// 設定顏色一定要帶 Tag ，不然會被蓋過
				@include bem-tag {
					color: #999;
				}

				@include breakpoint-hover {
					&:hover,
					&:focus {
						color: $primary-color;
					}
				}
			}

			&__block {
				&[data-layout='recommend'] {
					display: flex;

					#{$el} {
						&__title {
							white-space: nowrap;
							margin-right: rem-calc(7);
						}

						&__list {
							margin-top: rem-calc(-2); // 把第一排padding回收
						}

						&__item {
							&:not(:last-child) {
								position: relative;

								// 分隔線
								&::after {
									content: '';
									position: absolute;
									right: 0;
									top: 50%;
									width: 1px;
									height: 10px;
									margin-top: rem-calc(-5);
									background: #999;
								}
							}
						}

						&__link {
							margin-right: rem-calc(13 - 7);
							margin-left: rem-calc(13 - 7);
							// padding: rem-calc(3 7);
						}
					}
				}

				&[data-layout='keyword'] {
					display: flex;

					#{$el} {
						&__link {
							margin-right: rem-calc(-10);
							margin-left: rem-calc(-10);
							padding-right: rem-calc(10);
							padding-left: rem-calc(10);
						}
					}
				}
			}
		}

		&.is-focus {
			background: $white;
			border-top-right-radius: $search-border-radius;
			border-top-left-radius: $search-border-radius;
			border-color: #ddd;

			#{$root} {
				&__input-box {
					&::before {
						content: '';
						position: absolute;
						left: 20px;
						bottom: 0;
						width: calc(100% - 40px);
						height: 1px;
						background: #ccc;
					}
				}

				&__input {
					border-color: transparent;
				}

				&__result {
					display: block;
				}
			}
		}
	}
}

// --------------------------------------------------------

/* Footer */
.ft {
	$root: &;

	position: relative;
	padding-top: rem-calc(110);
	font-size: rem-calc(13);
	color: $white;

	@include breakpoint(medium) {
		font-size: rem-calc(15);
	}

	@include breakpoint(large) {
		padding-top: rem-calc(146);
	}

	a {
		color: $white;
	}

	@at-root %ft-bg {
		position: absolute;
		z-index: -1;
		bottom: 0;
		left: 0;
		width: 100%;
		height: calc(100% + #{rem-calc(90)});
		pointer-events: none;

		@include breakpoint(large) {
			height: calc(100% + #{rem-calc(190)});
		}
	}

	// 背景圖片還沒Load進來的假背景
	// 為了要可以淡出，所以設置在::before
	&::before {
		@extend %ft-bg;

		content: '';
		background-image: linear-gradient(
			to top,
			#005379 -3%,
			#1dc9a3 48%,
			rgba(38, 208, 126, 0.2) 76%,
			rgba(38, 208, 124, 0)
		);
		transition: opacity 0.3s;
	}

	&.loaded-content {
		&::before {
			opacity: 0;
		}
	}

	&__bg-box {
		@extend %ft-bg;

		background-repeat: repeat-x;
		background-position: right bottom;
		background-size: auto 100%;

		&.loaded {
			background-image: url('../images/footer_bg_s.svg');
		}

		@include breakpoint(medium down) {
			&::before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-repeat: repeat-x;
				background-position: bottom right;
				background-size: 100%;
			}

			&.loaded {
				&::before {
					background-image: url('../images/footer_bg_s_deco.svg');
				}
			}
		}

		@include breakpoint(large) {
			&.loaded {
				background-image: url('../images/footer_bg--uc.png');
			}
		}
	}
}

// Footer - Sitemap
.ft-sitemap {
	display: flex;
	flex-wrap: wrap;

	&__block {
		width: 50%;
		margin-bottom: rem-calc(40);

		@include breakpoint(large) {
			width: 25%;
			text-align: center; // 為了要內容置中(1/3)
		}
	}

	&__title {
		font-weight: $font-bold-b;
		margin-bottom: rem-calc(22);
	}

	&__cont {
		@include breakpoint(large) {
			display: inline-block; // 為了要內容置中(2/3)
			text-align: left; // 為了要內容置中(3/3)
		}
	}

	// <ul>
	&__list {
		list-style: none;
		margin: 0;
		margin-top: rem-calc(-4); // 為了把 link padding-top 收回

		@include breakpoint(medium) {
			margin-top: rem-calc(-8); // 為了把 link padding-top 收回
		}
	}

	&__link {
		display: block;
		padding: rem-calc(4) 0;
		font-weight: $font-bold-m;

		@include breakpoint(medium) {
			padding: rem-calc(8) 0;
		}

		@include breakpoint-hover {
			position: relative;
			transition: transform 0.3s;

			&:hover,
			&:focus {
				font-weight: $font-bold-b;
				transform: translateX(15px);
			}
		}
	}
}

// 下載APP（QR Code, App Store, Google Play）
.ft-download {
	&__item {
		@include breakpoint(large down) {
			&:not(:last-child) {
				margin-bottom: rem-calc(13);
			}
		}
	}

	&__link {
		display: block;
	}

	&__img {
		display: block;
		width: 100%;
		max-width: rem-calc(190);
		height: auto;
		max-height: rem-calc(59);
	}

	.type-qr {
		img {
			max-width: rem-calc(140);
			max-height: rem-calc(140);
			border-radius: 12px;
		}

		@include breakpoint(large down) {
			display: none;
		}
	}

	.type-apple,
	.type-google {
		@include breakpoint(xlarge) {
			display: none;
		}
	}
}

// Footer 底部
.ft-bottom {
	display: flex;
	flex-direction: column;
	margin-right: rem-calc(20);
	margin-left: rem-calc(20);
	font-size: rem-calc(13);

	@include breakpoint(medium) {
		align-items: center;
		justify-content: space-between;
		flex-direction: row-reverse;
		margin-right: rem-calc(40);
		margin-left: rem-calc(40);
		padding-top: rem-calc(43 / 2);
		padding-bottom: rem-calc(54 / 2);
		border-top: 1px solid $white;
	}

	@include breakpoint(large) {
		padding-top: rem-calc(43);
		padding-bottom: rem-calc(54);
	}

	&__l {
		@include breakpoint(small only) {
			margin-top: rem-calc(40);
			padding-top: rem-calc(17);
			padding-bottom: rem-calc(30);
			border-top: 1px solid $white;
		}
	}

	&__r {
		$el: &;

		// Fixed IE Bug: 要給寬，要不然會沒有寬 => 超出螢幕
		@at-root .ie {
			#{$el} {
				flex: 0 0 185px;
			}
		}
	}
}

// 語言
.ft-lang {
	display: flex;
	align-items: center;

	&__cont {
		flex: 1;
	}

	&__title {
		margin-right: rem-calc(10);
	}

	&__select {
		@include select-style(true);

		margin-bottom: 0;
		height: rem-calc(33);
		font-size: rem-calc(13);
		color: $white;
		background-color: transparent;
		border: 1px solid $white;
		border-radius: rem-calc(6);

		&:focus {
			color: $primary-dark;
			background-image: url('#{$select-arrow}');
		}
	}
}
