/* Product Filter Panel (Foundation Component "Reveal") */
.filter-panel {
	$padding-y: #{rem-calc(30)};

	padding-top: $padding-y;
	padding-bottom: $padding-y;

	&__group {
	}

	&__choice-group {
		display: flex;
		margin: 0;
		margin-bottom: rem-calc(20 - 11); // 11 === label margin-bottom
		list-style: none;
	}

	&__choice {
		position: relative;
	}

	&__btn-box {
		display: flex;
		margin-top: rem-calc(40 - 20); // Choice Group

		.btn {
			margin-bottom: 0;

			&:not(:last-child) {
				margin-right: rem-calc(11);
			}
		}
	}

	&__btn-reset {
		min-width: 90px;
	}

	&__btn-submit {
		flex: 1;
	}
}
