// 【APP版本】 => 把相關物件隱藏
// ---------------------------------------- [START][Project] Hide APP
%hide-app {
	$el: &;

	// 如果 <html> 沒有加 .app 的 class 就會隱藏
	// => 避免 APP 相關物件，要另外寫display block
	@at-root html:not(.app) {
		#{$el} {
			display: none;
		}
	}
}
// ---------------------------------------- [END][Project] Hide APP

// --------------------------------------------------------

.hd-app {
	@extend %hide-app;
}

// Product List
// 列表中間的廣告
.pd-list-banner {
	@extend %hide-app;
}

// 問與答表單隱藏
.pd-ask__bottom,
.ask-input {
	@extend %hide-app;
}
