/* Checkbox */
.checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  margin-bottom: 0;
}

.checkbox + label {
  position: relative;
  margin-left: 0;
  padding-left: 28px;
  font-size: 0.9375rem;
  line-height: 1.2;
  color: #2b3843;
  word-break: break-all;
  word-break: break-word;
}

.checkbox + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0.6em;
  width: 20px;
  height: 20px;
  display: inline-block;
  background: #ffffff;
  border: 1px solid #aaa9a9;
  transform: translateY(-50%);
  transition: all 0.3s;
  border-radius: 0.3125rem;
}

.checkbox + label:after {
  content: '';
  position: absolute;
  border: 0 solid #fff;
  top: 0.6em;
  left: 3px;
  width: 0;
  height: 0;
  border: 0 solid #fff;
  border-top: 0 none;
  border-right: 0 none;
  transform: rotate(-45deg);
  transform-origin: left top;
}

.checkbox:checked + label {
  color: #2b3843;
}

.checkbox:checked + label::before {
  border-color: #26d07c;
  background: #26d07c;
}

.checkbox:checked + label:after {
  border-width: 2px;
  animation: checkbox-checked 0.5s forwards;
}

.checkbox:checked + label:hover {
  color: #2b3843;
}

.checkbox:checked + label:hover:before {
  border-color: #26d07c;
  background: #26d07c;
}

.checkbox:checked:focus + label {
  color: #2b3843;
}

.checkbox:checked:focus + label:before {
  border-color: #26d07c;
  background: #26d07c;
}

.checkbox:not(:checked) + label:hover:before {
  border-color: #26d07c;
  background: #f7fefb;
}

.checkbox:focus + label::before {
  background: #f7fefb;
  border-color: #26d07c;
  border-width: 2px;
}

.checkbox:disabled + label {
  cursor: not-allowed;
}

.checkbox:disabled + label:before {
  background: #ebedf0;
  border-color: #dedede;
}

.checkbox:disabled + label:hover:before {
  background: #ebedf0;
  border-color: #dedede;
}

@keyframes checkbox-checked {
  50% {
    width: 0;
    height: 8px;
  }
  100% {
    width: 12px;
    height: 8px;
  }
}

