/* Form 主要的設定項目 */

// 隱藏 Search 預設刪除按鈕
/* clears the 'X' from Chrome */
input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
	-webkit-appearance: none;
}

/* clears the 'X' from Internet Explorer */
input[type='search']::-ms-clear {
	display: none;
	width: 0;
	height: 0;
}
input[type='search']::-ms-reveal {
	display: none;
	width: 0;
	height: 0;
}

%field {
	padding: rem-calc(12px 14px);
	height: auto;
	font-size: rem-calc(15);
	border-radius: rem-calc(6);
	border: rem-calc(1) solid $bfLightGray;
	box-shadow: none;
	background-color: transparent;
	color: $primary-dark;

	&:focus {
		border-color: $primary-color;
		box-shadow: 0 0 0 rem-calc(1) $primary-color inset; // 加粗，畫面不會跳動的方法
		background-color: $white;
	}
}

// Input
.input {
	@extend %field;
}

.select {
	@extend %field;

	@include select-style;

	height: rem-calc(48);
}

.msg__info {
	display: none;
	left: 0;
	margin-top: rem-calc(8);
	font-size: rem-calc(12);
	font-weight: normal;
	line-height: 1.67;
	color: $bfSecondaryA;

	@include breakpoint(large) {
		margin-top: rem-calc(20);
		font-size: rem-calc(15);
		font-weight: $font-bold-m;
	}

	&.is-error {
		color: $bfDanger;
	}

	.show-msg & {
		display: block;
	}
}

%msg-color {
	border-color: $bfDanger;

	&:focus {
		box-shadow: 0 0 0 rem-calc(1) $bfDanger inset; // 加粗，畫面不會跳動的方法
	}

	&:focus-within {
		box-shadow: 0 0 0 rem-calc(1) $bfDanger inset; // 加粗，畫面不會跳動的方法
	}
}
.show-msg {
	input.input {
		&[type='text'],
		&[type='tel'],
		&[type='mail'],
		&[type='number'] {
			@extend %msg-color;
		}
	}

	select {
		@extend %msg-color;
	}

	.selectize-input {
		@extend %msg-color;
	}
}
