/* From - Radio (Button) */

/* 按鈕類型的 Checkbox / Radio
 * 增加於<input type="checkbox"> 或 <input type="radio"> 物件上 */
.checkbox-btn {
	@include checkbox-hide; // 隱藏預設樣式，外層要記得增加 position: relative

	+ label {
		$gutter: #{rem-calc(11)};

		margin: 0;
		margin-bottom: $gutter;
		margin-right: $gutter;
		padding: rem-calc(8 10);
		min-width: rem-calc(70);
		font-size: rem-calc(13);
		line-height: 1.4;
		text-align: center;
		color: #666;
		background-color: $white;
		border: solid 1px #ddd;
		border-radius: rem-calc(6);
	}

	&:checked {
		+ label {
			$bg-color: #2b3843;

			color: $white;
			background-color: $bg-color;
			border-color: $bg-color;
		}
	}
}
