// ★內頁 不需要 @include style.sass(layout)已經有
// 說明 (完整說明見) src/sass/style.sass
//	* 此檔案包含：內頁共用元件
//		- HTML Tag
//		- 表單(Form)相關元件
//		- 修改框架/套件
//		- keyframes

// --------------------------------------------------------

* {
	&,
	&::before,
	&::after {
		// safari 點擊會出現的灰色色塊
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	}

	// 移除Focus會出現藍色、黑色的框
	&:focus {
		outline: 0 none;
	}
}

/* Tag */

html,
body {
	overflow-x: hidden;
	overflow-y: auto;
}

body {
	color: $primary-dark;

	@include breakpoint($xs-down) {
		font-size: rem-calc(14);
	}

	@include breakpoint(small only) {
		font-size: rem-calc(15);
	}
}

img {
	pointer-events: none;
}

a,
button,
.btn {
	&:focus {
		outline: none;
	}
}

button {
	cursor: pointer;
}

// H1 - H6 設定
@each $size, $headers in $header-styles {
	@include breakpoint(small only) {
		@each $header, $header-defs in $headers {
			// $font-size-temp: 1rem;
			#{$header},
			.#{$header} {
				// $font-size-temp: rem-calc(map-get($header-defs, font-size));
				// font-size: $font-size-temp * 0.9;
			}
		}
	}
}

p {
	small {
		font-size: rem-calc(13);

		@include breakpoint(medium) {
			font-size: rem-calc(15);
		}
	}
}

// --------------------------------------------------------

/* 反選顏色 | Selection */
$selection-text-color: $white; // 反選的文字顏色
$selection-bg-color: $primary-color; // 反選的背景顏色

@mixin selection-color($text-color, $bg-color) {
	::selection {
		color: $text-color;
		background: $bg-color;

		// 當瀏覽器Tab非Focus狀態
		&:window-inactive {
			color: #323232;
			background: #c5c5c5;
		}
	}

	// Firefox版本: https://css-tricks.com/window-inactive-styling/#comment-92675
	// 但因為gulp-ruby-sass看不懂導致編譯錯誤(Invalid CSS)，所以無法使用
	// :-moz-window-inactive ::-moz-selection
	// 	color: #323232
	// 	background: #C5C5C5
}

// +selection-color(反選的文字顏色, 反選的背景顏色)
@include selection-color($selection-text-color, $selection-bg-color);

// 無法反選 | No Selection
.no-selection {
	@include selection-none;
}

// --------------------------------------------------------

// Custom Scroll Bar
// Mixin 移動到　src\sass\layout\_variable_mixin_function.scss
// 需使用則開啟下列樣式
@include breakpoint-hover {
	// Safari 設定 ::-webkit-scrollbar 會影響頁面寬度，如果內容Breakpoint設定在1440，MacBook 13吋 畫面就會一直跳動
	// 解決方法，把 Safari 屏除...
	.chrome,
	.firefox,
	.edge {
		@include custom-scroll-bar(#ccc);
	}

	.custom-scrollbar {
		@include custom-scroll-bar(#ccc);
	}
}

