/* Text */

.text-em {
	&,
	a {
		color: $primary-color;
	}
}

.text-dark {
	&,
	a {
		color: $primary-dark;
	}
}

.text-orange {
	&,
	a {
		color: $bfSecondaryA;
	}
}

.text-tip {
	color: $color-tip-dark;
}

.text-thin {
	font-weight: 300;
}

.text-bold {
	font-weight: $font-bold;
}

.text-weight-normal {
	font-weight: normal;
}

.text-underline {
	text-decoration: underline;
}

// -------------------------

// 文字溢行/文字點點點
%clamp {
	$line-height: 1.5;

	position: relative;
	display: block;
	display: -webkit-box;
	overflow: hidden;
	max-width: 100%;
	line-height: $line-height;
	text-overflow: ellipsis;
	-webkit-box-orient: vertical;
}

.clamp {
	&,
	&-1 {
		@extend %clamp;

		display: block;
		max-width: 100%;
		white-space: nowrap;
	}

	@function limit-height-get($i, $line-height) {
		@return calc(1em * #{$line-height} * #{$i});
	}

	@for $i from 2 through 6 {
		&-#{$i} {
			@extend %clamp;

			-webkit-line-clamp: $i;
			max-height: limit-height-get($i, 1.5);
			// max-height: limit-height-get($i, var(--line-height));
		}
	}
}

// --------------------------------------------------------

/* Image */

.img-absolute {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.img-rel {
	position: relative;
}

.img-deco {
	position: absolute;
	top: 0;
	left: 0;
}

/* object-fit */
// Fix the dimensions of an image and scale
// Default size: 1:1
//
// How to change size
// $imgWidth: // img width
// $imgHeight: // img height
// .object-fit:before{padding-top: round($imgHeight / $imgWidth * 100%)}
// round: round the number rounding
%object-fit-standard {
	position: relative;
	display: block; // Fixed <picture> bug
	overflow: hidden;

	&::before {
		content: '';
		display: block;
		padding-top: 100%;
	}

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100% !important;
		height: 100%;
		object-position: center center;
		background-position: center center;
	}
}

%object-fit-cover {
	img {
		object-fit: cover;
		background-size: cover;
	}
}

%object-fit-contain {
	background-position: center center;
	background-size: contain;

	img {
		object-fit: contain;
		background-size: contain;
	}
}

.object-fit {
	@extend %object-fit-standard;
	@extend %object-fit-cover;

	// 沒有內層(before)
	// &--wrap {
	// 	@extend %object-fit-standard;
	// 	@extend %object-fit-cover;

	// 	&::before {
	// 		display: none;
	// 	}
	// }

	// 應對不同比例的照片
	&-contain {
		@extend %object-fit-standard;
		@extend %object-fit-contain;
	}
}

// --------------------------------------------------------

/* Form */
.form {
	&__title {
		display: block;
		margin-bottom: rem-calc(20);
		font-size: rem-calc(16);
		font-weight: 500;
		line-height: 1.06;
		letter-spacing: normal;
		color: $bfBlueDark;

		&.small {
			font-size: rem-calc(14);
		}
	}

	.input__wrap {
		position: relative;
	}

	.currency {
		position: absolute;
		z-index: 1;
		top: 50%;
		left: rem-calc(14);
		transform: translateY(-50%);
		font-size: rem-calc(12);
		font-weight: normal;
		line-height: 1.67;
		letter-spacing: normal;
		color: $bfDarkGray;

		+ .input {
			padding-left: rem-calc(50);
			padding-right: rem-calc(14);
		}

		// 放置於後方
		&.back {
			left: auto;
			right: rem-calc(14);

			+ .input {
				padding-left: rem-calc(14);
				padding-right: rem-calc(50);
			}
		}
	}
}

