/* Section */

// 區塊內容
.sec {
	padding-top: rem-calc(20);
	padding-bottom: rem-calc(20);

	@include breakpoint(medium) {
		padding-top: rem-calc(35);
		padding-bottom: rem-calc(35);
	}

	@include breakpoint(large) {
		padding-top: rem-calc(50);
		padding-bottom: rem-calc(50);
	}

	// 給 web 往下推距離的
	&.sec-bottom-space-for-web {
		margin-bottom: rem-calc(74);
	}
}

// 第一塊內容
%sec-first {
	$header-height: 56;
	$sec-padding-top: 20;
	$adjust: 13; // 補回高度

	margin-top: rem-calc($header-height - $sec-padding-top + $adjust);

	@include breakpoint(medium) {
		$sec-padding-top: 35;

		margin-top: rem-calc($header-height - $sec-padding-top + $adjust);
	}

	@include breakpoint(large) {
		$header-height: 126;
		$sec-padding-top: 50;

		margin-top: rem-calc($header-height - $sec-padding-top + $adjust);
	}
}

// 最後一塊內容
%sec-final {
	padding-bottom: rem-calc(80);

	@include breakpoint(large) {
		padding-bottom: rem-calc(124);
	}
}

.sec-first {
	@extend %sec-first;
}

.sec-final {
	@extend %sec-final;
}

.sec-first-final {
	@extend %sec-first;
	@extend %sec-final;
}

.sec-title-box {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: rem-calc(14);

	@include breakpoint(medium) {
		margin-bottom: rem-calc(34);
	}

	&__title {
		flex: 1;
		margin-bottom: 0;
		font-weight: $font-bold-b;
		word-break: break-word;
		color: $title-dark;

		&.bean {
			@include breakpoint(medium) {
				&::before {
					@include font-icon-add(bean);

					margin-right: em-calc(5 / 34) + 0.2; // 0.2em 為原 Icon Font 有的 margin-right
					color: $primary-color;
					transform: scale(#{(37 / 34)});
				}
			}
		}
	}

	&__tip {
		margin-bottom: 0;
		font-size: rem-calc(12);
		color: #666;

		@include breakpoint(small only) {
			margin-top: rem-calc(5);
		}

		@include breakpoint(medium) {
			font-size: rem-calc(14);
		}
	}

	&__r {
		flex: 0 0 auto;
		margin-left: rem-calc(20);
		width: auto;

		.btn-icon {
			--margin-y: #{rem-calc(-11)}; // 回收上下高，避免影響標題區大小

			margin-top: var(--margin-y);
			margin-bottom: var(--margin-y);

			// 最後一個要收回 padding → 為了齊右
			&:last-child {
				margin-right: rem-calc(-8);
			}
		}

		.btn-text {
			margin-right: rem-calc(-5); // 回收 padding → 為了齊右
			padding: rem-calc(5);
			font-size: rem-calc(13);
			color: $bfDarkGray;

			@include breakpoint(medium) {
				font-size: rem-calc(15);
			}

			@include breakpoint-hover {
				&:hover,
				&:focus {
					color: $bfDark;
				}
			}
		}

		.text-arrow {
			margin-right: rem-calc(-9 - 2);
		}

		small {
			font-size: rem-calc(13);

			@include breakpoint(medium) {
				font-size: rem-calc(15);
			}
		}

		.text-tip {
			white-space: nowrap;
		}
	}

	.btn-icon {
		color: #666;
	}

	&.sm-margin {
		@include breakpoint(medium) {
			margin-bottom: rem-calc(20);
		}
	}
}

.breadcrumbs {
	&-wrapper {
		margin-bottom: rem-calc(32);

		@include breakpoint(medium) {
			margin-bottom: rem-calc(40);
		}
	}

	li {
		display: flex;
		font-size: rem-calc(13);
		color: $primary-dark;

		&::after {
			font-size: rem-calc(12);
			color: $primary-dark-05 !important;
		}

		// Home
		&:first-child {
			a {
				position: relative;
				padding-left: em-calc(20 / 13);

				.be-icon {
					position: absolute;
					top: 50%;
					left: 0;
					transform: translateY(-50%);
					font-size: rem-calc(17);

					&::before {
						margin: 0;
					}
				}
			}
		}

		&:not(:last-child) {
			&::after {
				@include font-icon-add(arrow-right, false);

				@include breakpoint(small down) {
					margin: rem-calc(0 10);
				}
			}
		}

		&:last-child {
			font-weight: $font-bold-m;
		}
	}

	// span {
	// 	max-width: 10em;
	// 	display: block;
	// 	overflow: hidden;
	// 	text-overflow: ellipsis;
	// 	white-space: nowrap;
	// }

	a {
		color: $primary-dark-05;
	}

	// 太長點點點
	@mixin limit-text-width {
		span {
			max-width: 10em;
			display: block;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
	}

	@at-root %limit-text-width {
		@include limit-text-width;
	}

	&.text-white-small-only {
		a {
			@include breakpoint(small only) {
				color: rgba($white, 0.5);
			}
		}

		li {
			@include breakpoint(small only) {
				&::after {
					color: rgba($white, 0.5);
				}

				color: rgba($white, 0.5);

				&:last-child {
					color: $white;
				}
			}
		}
	}

	&.has-max-width {
		@extend %limit-text-width;
	}

	&.has-max-width-small-only {
		@include breakpoint(small only) {
			@include limit-text-width;
		}
	}

	&.has-max-width-medium-down {
		@include breakpoint(medium down) {
			@include limit-text-width;
		}
	}
}

// --------------------------------------------------------

/* Foundation */

.grid-container {
	@mixin grid-container-padding-x($paddingX, $bp: null) {
		@if $bp {
			@include breakpoint(#{$bp}) {
				$padding-x: #{rem-calc($paddingX)};

				padding-right: $padding-x;
				padding-left: $padding-x;
			}
		} @else {
			$padding-x: #{rem-calc($paddingX)};

			padding-right: $padding-x;
			padding-left: $padding-x;
		}
	}

	@include grid-container-padding-x($padding-x-global-s);
	@include grid-container-padding-x($padding-x-global-m, medium);
	@include grid-container-padding-x($padding-x-global-l, large);
	@include grid-container-padding-x($padding-x-global-xxl, xxlarge);

	@include breakpoint-between(large, 1440 - 1) {
		max-width: rem-calc(1120 + 100 * 2);
	}

	&-m {
		max-width: rem-calc(740) + $padding-x-global-m * 2;

		@include breakpoint(large) {
			max-width: rem-calc(740) + $padding-x-global-l * 2;
		}

		@include breakpoint(xxlarge) {
			max-width: rem-calc(740) + $padding-x-global-xxl * 2;
		}
	}
}

// Reveal
$reveal-panel-radius: #{rem-calc(10)};

.reveal-bottom-panel {
	position: fixed;
	top: auto !important; // 為了蓋過 Foundation JS 的 inline-style
	bottom: 0;
	height: auto;
	min-height: 0;
	border-top-left-radius: $reveal-panel-radius;
	border-top-right-radius: $reveal-panel-radius;
	animation: popup-from-bottom 0.3s;
}

@keyframes popup-from-top {
	0% {
		opacity: 0;
		transform: translateY(-100%);
	}

	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

@keyframes popup-from-bottom {
	0% {
		opacity: 0;
		transform: translateY(100%);
	}

	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

// 只有標題跟按鍵組的彈窗
.reveal-msg {
	$root: &;

	padding: rem-calc(20);
	max-width: rem-calc(600);
	border-radius: rem-calc(20);

	@include breakpoint(small only) {
		$left: #{rem-calc(38)};

		left: $left;
		width: calc(100% - #{$left} * 2);
		height: auto;
		min-height: auto;
	}

	@include breakpoint(medium) {
		left: 0 !important;
		max-width: rem-calc(320);
		// min-height: 200px;
	}

	&__title {
		margin-top: rem-calc(16);
		margin-bottom: rem-calc(30);
		font-size: rem-calc(18);

		+ #{$root} {
			&__desc {
				margin-top: rem-calc(-30 + 14);
			}
		}

		em {
			font-style: normal;
			color: $primary-color;
		}
	}

	&__desc {
		margin-bottom: rem-calc(30);
	}

	.button-box {
		justify-content: center;
		align-items: center;
		flex-wrap: nowrap;

		.btn {
			@include breakpoint($xs down) {
				min-width: 50%;
				padding-left: rem-calc(10);
				padding-right: rem-calc(10);
			}

			@include breakpoint($xs-down) {
				width: 100%;
			}

			@include breakpoint(medium) {
				min-width: rem-calc(125);
				font-size: rem-calc(18);

				&:not(:first-child) {
					margin-left: rem-calc(10);
				}
			}
		}

		@include breakpoint($xs-down) {
			flex-wrap: wrap;
			flex-direction: column-reverse;
		}
	}
}

.shadow-card {
	@at-root %shadow-card {
		background: #fff;
		box-shadow: $shadow-blue;
	}

	@extend %shadow-card;

	&-round {
		@extend %shadow-card;

		border-radius: rem-calc(6);
	}
}

// --------------------------------------------------------

// Popup - 商品規格
.pd-spec {
	$padding-x: rem-calc(10);
	$padding-x-reverse: $padding-x * -1;

	height: auto;
	min-height: auto;
	border-radius: rem-calc(20);
	padding: rem-calc(20);

	@include breakpoint(small down) {
		$left: #{rem-calc(37)};

		left: $left;
		width: calc(100% - #{$left} * 2);
	}

	@include breakpoint($xs-down) {
		$left: #{rem-calc(20)};

		left: $left;
		width: calc(100% - #{$left} * 2);
	}

	&__title {
		margin-bottom: rem-calc(22);

		@include breakpoint(medium) {
			font-size: rem-calc(18);
		}
	}

	&__list {
		margin-bottom: 0;
		margin-right: $padding-x-reverse;
		margin-left: $padding-x-reverse;
		padding-right: $padding-x;
		padding-left: $padding-x;

		@include breakpoint-hover {
			// Scroll Bar Change
			$scrollbar-thumb-color: #ccc; // 操作顏色
			$scrollbar-track-color: #eee; // 滾軸背景
			$scrollbar-width: 3px; // 寬度(瀏覽器預設為15px)

			@include custom-scroll-bar($scrollbar-thumb-color, $scrollbar-track-color, $scrollbar-width);
		}
	}

	&__item {
		display: flex;
		margin-bottom: rem-calc(14);
		font-size: rem-calc(13);

		&:last-child {
			margin-bottom: rem-calc(20);
		}
	}

	&__sub-title {
		margin-bottom: 0;
		margin-right: rem-calc(12);
		max-width: 2.06em; // ios safari 一個字寬是 13.27px 會被換行
		font-size: inherit;
	}

	&__desc {
		flex: 1;
	}

	&__block {
		padding-right: $padding-x;
		padding-left: $padding-x;
		font-size: rem-calc(13);

		&:not(:first-child) {
			margin-top: rem-calc(30);
		}
	}

	&__block-title {
		font-size: rem-calc(13);
	}

	&__able-list {
		list-style: none;
		margin: 0;
		margin-bottom: rem-calc(-8);
		margin-right: rem-calc(-6);
		margin-left: rem-calc(-6);
	}

	&__able-item {
		display: inline-block;
		margin-bottom: rem-calc(8);
		margin-right: rem-calc(6);
		margin-left: rem-calc(6);

		&::before {
			@include font-icon-add(check-circle);

			margin-right: rem-calc(6);
		}

		&.is-disabled {
			opacity: 0.4;
		}
	}

	&.reveal {
		[data-scroll-cont] {
			@include breakpoint(small down) {
				max-height: 55vh;
				overflow-x: hidden;
				overflow-y: auto;
			}
		}
	}
}

// 星等物件
.pd-star {
	color: $primary-orange;

	// 星等（空）符號
	.star {
		line-height: 1;

		&::before {
			@include font-icon-add('star');
		}
	}

	// 星等（滿）項目
	@at-root %star-fill {
		&::before {
			@include font-icon-add('star-fill', false);
		}
	}

	@for $i from 1 through 6 {
		&[data-star='#{$i}'] {
			.star {
				@for $j from 1 through $i {
					&:nth-child(#{$j}) {
						@extend %star-fill;
					}
				}
			}
		}
	}
}

