// --------------------------------------------------------

/* Button */

.btn {
	padding: em-calc(10 / 16) rem-calc(16);
	font-size: rem-calc(16);
	font-weight: $font-bold;
	line-height: 1.2;
	border-radius: rem-calc(6);
	min-width: rem-calc(138);

	@include breakpoint(medium) {
		min-width: rem-calc(170);
		padding-top: 10px;
		padding-bottom: 10px;
		font-size: rem-calc(20);
	}

	&.alert {
		&,
		&:hover,
		&:focus {
			color: $white;
		}
	}

	&.warning-gradient {
		margin: rem-calc(20) 0 0;
		min-width: 0;
		color: $white;
		background-image: $primary-gradient-orange;
		border: 0 none;
		border-radius: rem-calc(6);
	}

	&.no-border {
		&,
		&.hollow {
			border-color: transparent;
		}

		&.secondary {
			border-color: transparent;
		}

		&.hollow.secondary {
			border-color: transparent;
		}
	}

	&.btn-small {
		font-size: rem-calc(13);
		font-weight: $font-bold-m;
		border-width: rem-calc(1);

		@include breakpoint(medium) {
			font-size: rem-calc(15);
		}
	}

	&.w-full {
		width: 100%;
	}

	// disabled
	&.disabled,
	&[disabled] {
		opacity: 1;
		background-color: $bfLightGray;

		&:hover,
		&:active {
			background-color: $bfLightGray;
		}
	}
}

.btn-icon {
	display: inline-block;
	padding: rem-calc(4 5); // 44px * 43.9px
	width: rem-calc(44);
	font-size: rem-calc(24);
	line-height: 1.5;
	text-align: center;
}

/* 浮動在右側的黃色按鈕 */
.btn-right-yellow {
	position: absolute;
	right: $padding-x-global-s * -1;
	top: 0;
	margin-bottom: 0;
	padding-right: rem-calc(8);
	padding-left: rem-calc(12);
	min-width: 0;
	font-size: rem-calc(14);
	white-space: nowrap;
	background-image: $primary-gradient-orange;
	border: 0 none;
	border-radius: 99px;
	border-top-right-radius: 0;
	border-bottom-right-radius: 0;

	@include breakpoint(medium) {
		right: $padding-x-global-m * -1;
	}
}

/* 多個 button */
.button-box {
	.btn {
		@include breakpoint($xs-down) {
			margin: 0;

			// SIT F116 - 上下排時重要按鈕要在上，所以更改設置
			&:not(:last-child) {
				margin: 0;
				margin-top: rem-calc(16);
			}
		}

		@include breakpoint($xs) {
			margin-bottom: 0;

			&:not(:first-child) {
				margin-left: rem-calc(10);
			}
		}

		@include breakpoint(medium) {
			margin-bottom: 0;

			&:not(:first-child) {
				margin-left: rem-calc(20);
			}
		}
	}

	&.nowrap {
		flex-wrap: nowrap;
	}

	// cart use
	&.w-full-for-medium {
		@include breakpoint(small down) {
			.btn {
				margin-left: 0;
				width: 100%;

				&:not(:last-child) {
					margin-bottom: rem-calc(12);
				}
			}
		}
	}
}

// --------------------------------------------------------

/* Arrow */
.text-arrow {
	padding: rem-calc(9);
	font-size: rem-calc(13);
	font-weight: $font-bold;
	color: $primary-color;
	white-space: nowrap;

	@include breakpoint(medium) {
		font-size: rem-calc(15);

		&.font-small {
			font-size: rem-calc(16);
		}
	}

	&.right {
		&::after {
			@include font-icon-add(arrow-right);
		}
	}

	&.left {
		&::before {
			@include font-icon-add(arrow-left);
		}
	}

	@include breakpoint-hover {
		@at-root %ani-tsn {
			transition: transform 0.3s;
		}

		@at-root %ani-right {
			transform: translateX(5px);
		}

		@at-root %ani-left {
			transform: translateX(-5px);
		}

		.be-icon {
			&::before {
				@extend %ani-tsn;
			}
		}

		&.right {
			&::after {
				@extend %ani-tsn;
			}
		}

		&.left {
			&::before {
				@extend %ani-tsn;
			}
		}

		&:hover,
		&:focus {
			.be-icon-arrow-right {
				&::before {
					@extend %ani-right;
				}
			}

			.be-icon-arrow-left {
				&::before {
					@extend %ani-left;
				}
			}

			&.right {
				&::after {
					@extend %ani-right;
				}
			}

			&.left {
				&::before {
					@extend %ani-left;
				}
			}
		}
	}
}

.btn-back {
	display: inline-block;
	margin-top: rem-calc(-9);
	margin-bottom: rem-calc(12);
	margin-left: rem-calc(-9);
}

// --------------------------------------------------------

// 加載更多按鈕
.btn-load-more {
	$root: &;

	position: relative;
	display: block;
	margin: 0 auto;
	font-size: rem-calc(13);
	font-weight: $font-bold-m;
	color: $color-tip;

	&::after {
		@include font-icon-add(arrow-bottom);

		display: block;
		margin: 0 auto;
		margin-top: rem-calc(8);
		font-size: rem-calc(12);
	}

	&__dots {
		opacity: 0;
		position: absolute;
		left: 50%;
		top: 50%;
		display: block;
		transform: translate(-50%, -50%);
	}

	&__dot {
		$gutter: rem-calc(4);
		$gutter-half: calc(#{$gutter} / 2);
		$size: rem-calc(4);

		display: inline-block;
		margin-right: $gutter-half;
		margin-left: $gutter-half;
		width: $size;
		height: $size;
		background: #26d07c;
		border-radius: 9px;
		animation: ani-dot 1s infinite alternate;
		animation-play-state: paused; // 為了要符合 IE ，這項一定要單獨拆出來

		@for $i from 2 through 3 {
			&.dot-#{$i} {
				animation-delay: 0.12s * ($i - 1);
			}
		}
	}

	@keyframes ani-dot {
		0% {
			transform: scale(1);
		}

		100% {
			transform: scale(0.3);
		}
	}

	@include breakpoint(medium) {
		font-size: rem-calc(16);

		&::after {
			font-size: rem-calc(18);
			margin-top: rem-calc(5);
		}
	}

	@include breakpoint-hover {
		&:hover,
		&:focus {
			&::after {
				animation: ani-arrow(bottom);
			}
		}
	}

	// 按下去後，跑Loading樣式
	&.is-loading {
		pointer-events: none;

		@at-root %hide-el {
			@include selection-none;

			opacity: 0 !important;
			clip: rect(0, 0, 0, 0) !important;
		}

		&::after {
			@extend %hide-el;

			animation: none !important;
		}

		#{$root} {
			&__text {
				@extend %hide-el;
			}

			&__dots {
				opacity: 1;
			}

			&__dot {
				animation-play-state: running;
			}
		}
	}
}

// --------------------------------------------------------

/* Label */
.label {
	padding: rem-calc(2 6 2);
	font-size: rem-calc(12);
	font-weight: $font-bold;
	color: rgba(#999, 0.9);
	background: transparent;
	border: 1px solid rgba(#999, 0.9);
	border-radius: rem-calc(4);

	@include breakpoint(medium) {
		padding: rem-calc(3 7 4);
		font-size: rem-calc(14);
	}

	&.default {
		color: #666666;
		background-color: transparent;
		border-color: #666666;
	}

	&.warning {
		color: $bfSecondaryA;
		background-color: transparent;
		border-color: currentColor;
	}

	&.error {
		color: $bfDanger;
		background-color: transparent;
		border-color: currentColor;
	}

	&.success {
		color: $primary-color;
		background-color: transparent;
		border-color: $primary-color;
	}
}

// --------------------------------------------------------

/* Wave */
.wave-box {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 156px;
	pointer-events: none;

	canvas {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

// --------------------------------------------------------

/* Help */

.mt-0 {
	margin-top: 0;
}

.mb-0 {
	margin-bottom: 0;
}

.pt-0 {
	padding-top: 0;
}

.pb-0 {
	padding-bottom: 0;
}

// 定義 SVG 隱藏於頁面使用
.svg-def {
	position: absolute;
	top: -9999px;
	left: -9999px;
	overflow: hidden;
	width: 0;
	height: 0;
	opacity: 0;
	pointer-events: none;
	speak: never;
}

// 隱藏
.show-for-app {
	display: none !important;
}

// --------------------------------------------------------

/* Keyframes */
@keyframes arrow-opacity {
	0%,
	20%,
	80%,
	100% {
		opacity: 1;
	}

	50%,
	51% {
		opacity: 0;
	}
}

@keyframes arrow-to-right {
	0%,
	100% {
		transform: translateX(0);
	}

	50% {
		transform: translateX(10px);
	}

	51% {
		transform: translateX(-10px);
	}
}

@keyframes arrow-to-left {
	0%,
	100% {
		transform: translateX(0);
	}

	50% {
		transform: translateX(-10px);
	}

	51% {
		transform: translateX(10px);
	}
}

@keyframes arrow-to-top {
	0%,
	100% {
		transform: translateY(0);
	}

	50% {
		transform: translateY(-10px);
	}

	51% {
		transform: translateY(10px);
	}
}

@keyframes arrow-to-bottom {
	0%,
	100% {
		transform: translateY(0);
	}

	50% {
		transform: translateY(10px);
	}

	51% {
		transform: translateY(-10px);
	}
}

// --------------------------------------------------------

// 列表背景黑色漸層
.cont-bg-gradient {
	position: relative;

	&::before {
		content: '';
		pointer-events: none;
		display: block;
		position: absolute;
		z-index: 0;
		top: 0;
		left: 0;
		width: 100%;
		height: rem-calc(130);
		background-image: linear-gradient(to bottom, rgba($content-bg-color, 0.4) 60%, rgba($content-bg-color, 0) 99%);
	}
}

// --------------------------------------------------------

// -------------------------------------------------------
// GotoTop
.gotop {
	@include fc(center, center);

	flex-direction: column;
	position: fixed;
	z-index: 100;
	bottom: rem-calc(15);
	right: rem-calc(20);
	overflow: hidden;
	width: rem-calc(32);
	height: rem-calc(32);
	font-size: rem-calc(12);
	border-radius: 50%;
	color: $white;
	background-color: $primary-color-lighten;
	transition: 0.5s all;

	opacity: 0;
	pointer-events: none;
	transform: translateY(#{rem-calc(30)});

	&:hover,
	&:focus {
		color: $white;
	}

	.be-icon {
		padding: rem-calc(5 0);
		font-size: 1em;
		line-height: 1;
		transform: translateY(#{rem-calc(-3)});

		@include breakpoint(xlarge down) {
			font-size: rem-calc(20);
			transform: translateY(0);
		}
	}

	&_txt {
		font-size: inherit;
		line-height: 1;
		letter-spacing: rem-calc(1);
		transform: translateY(#{rem-calc(-6)});

		@include breakpoint(xlarge down) {
			transform: scale(0.66) translateY(#{rem-calc(-3)});
			transform-origin: top;
		}
	}

	@include breakpoint-hover {
		transition: 0.5s;

		&:hover,
		&:focus {
			// color: $primary-txt;
			background-color: $primary-color;
		}
	}

	// 桌機版才有
	@include breakpoint(large) {
		bottom: rem-calc(80);
		right: rem-calc(80);
		width: rem-calc(48);
		height: rem-calc(48);
		font-size: rem-calc(14);

		html.is-collapse & {
			transform: translateY(0);
			opacity: 1;
			pointer-events: all;
		}
	}
}

// -------------------------------------------------------
// 需要提醒可以 scroll 的區塊
.enable-scroll {
	position: relative;

	[data-scroll-cont] {
		&::after {
			pointer-events: none;
			content: '';
			display: block;
			position: absolute;
			bottom: rem-calc(-10);
			left: 0;
			width: 100%;
			height: rem-calc(50);
			background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), #fff 50%);
			transition: 0.24s opacity;
		}

		&.is-end {
			&::after {
				opacity: 0;
			}
		}
	}
}

